@font-face {
  font-family: "Malgun-gothic";
  font-weight: normal;
  src: url("components/fonts/malgun.ttf") format("truetype");
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: black;
  font-family: Malgun-gothic;
}

.App {
  margin: 1rem;
}

.App .inputForm {
  margin: 2rem;
}

.App > div {
  display: flex;
  width: 100%;
}

.App #result-list {
  width: 60%;
  height: 500px;
  overflow: auto;
}

.App #pagination {
  margin-top: 3rem;
}

.App #pagination a {
  color: black;
  font-size: 20px;
  text-decoration: none;
  margin: 0 10px;
}

.App #pagination a.on {
  color: lightblue;
  font-weight: bold;
}